/* Featured Posts */
.featured-posts {
  margin-top: 40px;
}

.featured-post {
  display: flex;
  align-items: center;
  border: 1px solid #444;
  border-radius: 12px;
  margin-bottom: 20px;
  overflow: hidden;
  transition: box-shadow 0.3s; /* Add transition effect for box shadow on hover */
}

.featured-post:hover {
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2); /* Apply box shadow on hover */
}

.featured-post img {
  width: 300px;
  height: 200px;
  object-fit: cover;
}

.post-details {
  padding: 20px;
}

.post-details h3 {
  color: #ffdb01;
}

.post-date {
  color: #999;
}
