/* Hero Section */
.hero-section {
  margin-bottom: 40px;
  color: #fff;
  transition: background-color 0.3s; /* Add transition effect for background color change */
}

.hero-section:hover {
  background-color: #333; /* Change background color on hover */
}

h2 {
  color: #ffdb01;
}

p {
  color: #999;
}

.cta-button {
  padding: 10px 20px;
  background-color: #ffdb01;
  color: #121212;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.cta-button:hover {
  background-color: #ff851f;
}
