/* Navigation Bar */
nav {
  background-color: #121212;
  padding: 10px;
}

nav a {
  color: #fff;
  text-decoration: none;
  margin: 0 15px;
  transition: color 0.3s; /* Add transition effect for smoother color change */
}

nav a:hover {
  color: #ffdb01; /* Change color on hover */
}


  