/* Blog Post */
.blog-post {
  max-width: 800px;
  margin-bottom: 40px;
  padding: 30px;
  border: 1px solid #444;
  border-radius: 12px;
  background-color: #1a1a1a;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2);
  color: #fff;
  transition: box-shadow 0.3s; /* Add transition effect for box shadow on hover */
}

.blog-post:hover {
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.3); /* Apply box shadow on hover */
}

h2 {
  color: #ffdb01;
}

.post-date,
.author-info,
.tags {
  color: #999;
}

.comments {
  margin-top: 20px;
}

h3 {
  color: #ffdb01;
}

.comment {
  margin-bottom: 15px;
}

.comment p {
  color: #999;
}
