/* Newsletter Subscription */
.newsletter-subscription {
  margin-top: 20px;
  text-align: center;
  color: #fff;
}

h2 {
  color: #ffdb01;
}

input[type="email"] {
  padding: 10px;
  margin-right: 10px;
  border: 1px solid #ffdb01;
  border-radius: 4px;
  width: 200px;
}

button {
  padding: 10px 20px;
  background-color: #ffdb01;
  color: #121212;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s; /* Add transition effect for button color change */
}

button:hover {
  background-color: #ff851f; /* Change button color on hover */
}
