/* General Styling */

body {
  margin: 0;
  font-family: 'Arial', sans-serif;
  touch-action: manipulation;
  overflow-x: hidden;
  background-color: #121212;
}

/* Mobile Styles */
@media only screen and (max-width: 600px) {
  .App-header {
    padding: 10px; /* Adjust header padding for smaller screens */
  }

  .App-main {
    padding: 10px; /* Adjust main content padding for smaller screens */
  }

  .blog-post {
    max-width: 100%; /* Make blog posts full width on smaller screens */
    padding: 15px; /* Increase padding for better spacing */
    margin-bottom: 20px; /* Add more spacing between blog posts */
  }

  .cta-button,
  .newsletter-subscription input[type="email"],
  .newsletter-subscription button,
  .social-media-integration a,
  .footer-links a {
    width: 100%; /* Make buttons and form elements full width on smaller screens */
  }

  .newsletter-subscription input[type="email"] {
    padding: 10px;
    margin-right: 10px;
    border: 1px solid #ffdb01;
    border-radius: 4px;
    width: calc(100% - 20px); /* Adjust width to match the button's width */
    margin-bottom: 15px; /* Increase spacing between email input and button */
  }

  .featured-post {
    flex-direction: column; /* Stack featured posts content vertically on smaller screens */
  }

  .featured-post img {
    width: 100%; /* Make featured post images full width on smaller screens */
    height: auto; /* Maintain aspect ratio */
  }

  .post-details {
    padding: 15px; /* Adjust post details padding for smaller screens */
  }
}

.App {
  text-align: center;
  background-color: #121212; /* Dark background color */
  color: #fff; /* Light text color */
  min-height: 100vh;
}

.App-header {
  padding: 20px;
  background: linear-gradient(30deg, #ffdb01 0%, #ff851f 30%, #ff3d3d 52%, #ff0f6f 70%, #f0f 100%);
  color: #121212; /* Dark text color */
}

.App-main {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px;
}

h1 {
  font-size: 24px; /* Adjust header font size for better readability */
  color: #ffdb01; /* Hex theme color for header */
}

