/* Footer */
footer {
  background-color: #121212;
  padding: 20px;
  color: #fff;
}

.footer-links {
  margin-top: 20px;
}

.footer-links a {
  color: #ffdb01;
  text-decoration: none;
  margin-right: 24px; /* Increased margin for more spacing between links */
  transition: color 0.3s; /* Add transition effect for link color change */
}

.footer-links a:hover {
  color: #ff851f; /* Change link color on hover */
  text-decoration: underline;
}
