/* Social Media Integration */
.social-media-integration {
  margin-top: 20px;
  color: #fff;
}

a {
  display: inline-block;
  margin-right: 10px;
  color: #ffdb01;
  text-decoration: none;
  transition: color 0.3s; /* Add transition effect for icon color change */
}

i {
  font-size: 24px;
}

a:hover {
  color: #ff851f; /* Change icon color on hover */
}
